.works {
  padding-top: 45px;
  background-image: url(../imgs/works-bg.png);
  background-size: cover;
  min-height: 1000px;
  /* Slick Slider styles */
}

.works img {
  display: block;
  margin: 0 auto 35px auto;
}

.works h2 {
  max-width: 275px;
  text-align: center;
  margin: 0 auto 65px auto;
  color: #fefcfd;
  font-family: "Proxima Nova";
  font-size: 36px;
  font-weight: 600;
}

.works__slider {
  max-width: 760px;
  margin: 0 auto;
}

.works__slider-item {
  margin: 0 15px;
}

.works__gallery-btn {
  color: #fff;
  font-family: "PT Sans";
  font-size: 20px;
  font-weight: 300;
  font-style: italic;
  text-align: center;
  padding-top: 12px;
  display: block;
  width: 195px;
  height: 50px;
  border: 1px solid #fff;
  margin: 60px auto 0 auto;
}

.works__gallery-btn:hover {
  text-decoration: none;
  color: #4abc65;
  border: 1px solid #4abc65;
  background-color: #dadada;
  transition: all 0.7s;
}

.works .slick-prev {
  left: -210px;
}

@media all and (max-width: 1235px) {
  .works .slick-prev {
    left: -20px;
  }
}

.works .slick-prev::before {
  content: url(../imgs/arrow-left.svg);
}

.works .slick-next {
  right: -210px;
}

@media all and (max-width: 1235px) {
  .works .slick-next {
    right: -20px;
  }
}

.works .slick-next::before {
  content: url(../imgs/arrow-right.svg);
}

.works .slick-dots li {
  width: 10px;
  height: 10px;
  margin: 0 3px;
}

.works .slick-dots li button:before {
  height: 10px;
  width: 10px;
  border: 2px solid #fff;
  border-radius: 50%;
  content: '';
  opacity: 0.75;
}

.works .slick-dots li.slick-active button:before {
  background-color: #fff;
}

@media all and (max-width: 700px) {
  .works {
    min-height: 700px;
  }
}
